import React from 'react';
import { Container, Row, Col, Card, CardBody, CardTitle, CardText, FormGroup, Button } from 'reactstrap'; 
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';

const API_KEY = "6cca5d9ad193e48a8fe2393bb7ce85d3";


export class Development extends React.Component {
	state = {
		weathers : [],	
		alertNull: '',
		alert: '',
		loading: ''
	}

	getWeather = async (e) => {
		e.preventDefault();
		const keySearch = this.keySearch.value;
		const api_call = await fetch(`https://api.openweathermap.org/data/2.5/find?q=${keySearch}&units=metric&appid=${API_KEY}`);
		const data =  await api_call.json();
		console.log(data);

		{keySearch.length === 0 ? 
			this.setState({
				weathers: [],
				alertNull: 'please input city'
			}) 
			:
			this.setState({
				weathers: data.list,
				alertNull: ''
			})
		}

		this.addForm.reset();
	}


	componentDidMount(){
		setTimeout(() => {
			this.setState({
				loading: 'loading'
			})
		}, 3000)
	}

	render(){
		let alertNull = 
		<div className='alertNull'>
			<Row>
				<Col lg='3' className='mx-auto'>
					<CardText>
						{this.state.alertNull}
					</CardText>
				</Col>
			</Row>
		</div>

		let wrongName = 
		<div>
			{this.state.aler}
		</div>

		let animation = 
		<div className='bagAnimation'>
			<Row>
				<Col lg='3' className='mx-auto'>
					<Card>
						<CardBody>
							<h1> Welcome </h1>
							<div className='loading'>
							</div>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</div>

		return(
			
			<div className='Development container-fluid'>
				{this.state.loading.length === 0 ? animation : 
				<div className='WeatherMenu'>
					<Row>
						<Col lg='6' className='mx-auto'>
							<div className='headingTitle'>
			                  <h1> 
			                    <FontAwesomeIcon icon="cloud-sun" />
			                    WeatherInfo 
			                  </h1>
			                </div>
			                <Card>
				                <CardBody>
									<form onSubmit={(e) => (this.getWeather(e))} className='form' ref={(input) => (this.addForm) = input}>
										<Row form>
											<Col md='10'>
												<FormGroup>
													<input type='teks' ref={(input) => (this.keySearch) = input} className='form-control' 
													placeholder={this.state.weathers.length === 0 ? "Input Your City..." : "Search Again..."}/>
												</FormGroup>
											</Col>
											<Col md='2'>
												<FormGroup>
													<input type='submit' className='form-control' value='get'/>
												</FormGroup>
											</Col>
										</Row>
									</form>
								</CardBody>
							</Card>
						</Col>
					</Row>
					{alertNull}
					<Row>
						{this.state.weathers.map((weather)=>{
							return(
							<Col lg='3'>
								<Card className='mb-3'>
									<CardBody>
									<div key={weather.id}>
										<CardText> Location: {weather.name}, {weather.sys.country} </CardText>
										<CardText>  Temperature: {weather.main.temp} </CardText>
										<CardText>  Humdity: {weather.main.humidity} </CardText>
										<CardText>  Status: {weather.weather[0].main} </CardText>
										<CardText>  Description : {weather.weather[0].description} </CardText>
										<CardText>  Coordinates : [{weather.coord.lat}, {weather.coord.lon}] </CardText>
									</div>
									</CardBody>
								</Card>
							</Col>
							)
						})}
					</Row>
					
					<footer className="footer">
			          <Container fluid>
			            <Row>
			              <Col lg='6' md='6' sm='6' className="Copyright">
			                <Card>
			                  <CardBody>
			                      <i> Copyright © </i>
			                      <a href="https://portofolio-sandy.firebaseapp.com/" target="__blank"> sandy kurniawan </a>
			                  </CardBody>
			                </Card>
			              </Col>
			              <Col lg='6' md='6' sm='6' className="Powered">
			                <Card>
			                  <CardBody>
			                    <i> Powered: </i>
			                      <a href="https://openweathermap.org/" target="__blank"> openweathermap.org </a>
			                  </CardBody>
			                </Card>
			              </Col>
			            </Row>
			          </Container>
			        </footer>
			     </div>
			     }
			</div>

		)
	}
}